import SummaryBox from "./SummaryBox";
import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useDispatch, useSelector } from "react-redux";
import { addInitialNumbers, addDrewNumber } from "../actions/prizeAction";
import mathUtils from "../utils/mathUtils";
import { Modal } from "react-bootstrap";
import GameSettings from "./GameSettings";

const TombolaGame = () => {
  const { t } = useTranslation();
  const dispatch = useDispatch();

  const [showModal, setShowModal] = useState(true);

  const drewNumbers = useSelector(state => state.drewNumbers.present);
  //console.log(drewNumbers);

  const toggleModal = () => {
    setShowModal(!showModal);
  };

  //const initialNumbers2 = mathUtils.range(1, 90);
  const initialNumbers = mathUtils.range(1, 90);
  //console.log(initialNumbers);
  const gameNumber = mathUtils.chunkArray(initialNumbers, 15);

  useEffect(() => {
    dispatch(addInitialNumbers(initialNumbers));
  });

  //console.log(gameNumber);

  const handleDrawState = n => {};

  const handleNumberClick = number => {
    //console.log(number);
    dispatch(addDrewNumber(number));
    handleDrawState(number);
  };

  const SingleItems = ({ array }) => {
    return (
      <div key={array}>
        {array.map(chArray => (
          <div className="container-fluid" key={`container-${chArray}`}>
            <div className="row justify-content-between" key={chArray}>
              {chArray.map(item => (
                <button
                  className={`btn btn-outline-warning col mx-1 my-1 ${
                    drewNumbers.includes(item) ? "active none" : ""
                  }`}
                  key={item}
                  value={item}
                  onClick={() => handleNumberClick(item)}
                  data-toggle="button"
                  aria-pressed="false"
                  style={{
                    pointerEvents: drewNumbers.includes(item) ? "none" : ""
                  }}
                >
                  <div>{item}</div>
                </button>
              ))}
            </div>
          </div>
        ))}
      </div>
    );
  };

  const SingleBox = ({ array }) => {
    return (
      <>
        {array.map(box => (
          <div className="col-md-6 my-1 col-12" key={`box-${box}`}>
            <div className="card" key={box}>
              <div className="card-body">
                <SingleItems array={mathUtils.chunkArray(box, 5)} />
              </div>
            </div>
          </div>
        ))}
      </>
    );
  };

  const GameBox = () => {
    return (
      <div className="card">
        <div className="card-header">
          <div className="card-title">
            {t("game-page.actual-prize")}: {t("welcome-page.prizes.tombola")}
          </div>
        </div>
        <div className="card-body">
          <div className="container-fluid">
            <div className="row my-3">
              <SingleBox array={gameNumber} />
            </div>
          </div>
        </div>
      </div>
    );
  };

  const GameSettingsModal = ({ show, toggle }) => {
    return (
      <Modal show={show} onHide={toggle} backdrop={`static`} size="lg" centered>
        <Modal.Header closeButton>
          <Modal.Title>{t("generics.settings")}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <GameSettings />
        </Modal.Body>
        <Modal.Footer>
          <button className="btn btn-primary" onClick={toggle}>
            {t("generics.save")}
          </button>
        </Modal.Footer>
      </Modal>
    );
  };

  return (
    <div className="d-flex justify-content-center align-items-md-center">
      <div className="container-fluid">
        <div className="row my-2">
          <div className="col-xl-4  col-md-4 offset-md-0 col-12 order-md-1">
            <SummaryBox toggleModal={toggleModal} />
          </div>

          <div className="col-xl-8             col-md-8             col-12 order-md-0">
            <GameBox />
          </div>
        </div>
        <GameSettingsModal show={showModal} toggle={toggleModal} />
      </div>
    </div>
  );
};

export default TombolaGame;
